body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #e4dfda;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.contentBlock {
  height: 125px;
  background-color: #e4dfda;
  border-radius: 1vw;
  margin: 2vw;
  border: 2px solid #d4b483;
  text-align: center;
}

.contentHeading {
  color: #c1666b;
  font-weight: 800;
  padding-top: 1vw;
}

#modalImage {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

#AddListTask {
  width: 600px;
  margin: auto;
}

#ipRound {
  border-radius: 25px;
  border: 2px solid #c1666b;
  padding: 15px;
  width: 350px;
  height: 10px;
  outline: none;
}

#taskAddBtn {
  background-color: #c1666b;
  color: white;
  border-radius: 25px;
  border: 2px solid #c1666b;
  width: 75px;
  height: 33px;
  margin-left: 5px;
  font-weight: 600;
  outline: none;
}

.taskBoxHeading {
  text-align: center;
  background: #c1666b;
  border-radius: 20px;
  margin-top: 5px;
  color: white;
  font-weight: 600;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 1.2vw;
}

.taskPanding {
  background-color: #dcd3c5;
  border-radius: 20px;
  padding: 2px 2px 2px 5px;
  margin-top: 5px;
  font-weight: 600;
}

.taskComplete {
  background-color: skyblue;
  border-radius: 20px;
  padding: 2px 2px 2px 5px;
  margin-top: 5px;
  font-weight: 600;
}

#WeatherSelector {
  width: 390px;
  margin: auto;
}
#citySelector {
  height: 32px;
  border-radius: 25px;
  border: 2px solid #c1666b;
  width: 150px;
  font-weight: 700;
  outline: none;
  text-align-last: center;
}

.weatherTable {
  border: 3px solid #c1666b;
  border-collapse: collapse;
  margin: 0 auto;
  width: 50%;
}
.weatherTd1 {
  border: 2px solid #c1666b;
  padding: 5px;
  font-weight: 600;
  color: #c1666b;
}
.weatherTd2 {
  border: 2px solid #c1666b;
  padding: 5px;
  font-weight: 600;
}
#weatherCity {
  font-size: 5vw;
  color: #c1666b;
}
#CityTemp {
  font-size: 7vw;
}

